/* Estrutura geral */
.sidebar {
  width: 260px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  background-color: var(--bg-color);
  transition: background-color 0.4s ease, color 0.4s ease;
  border-right: 1px solid var(--border-color);
  position: relative;
}

/* Modos claro e escuro */
:root {
  --bg-color-light: #ffffff;
  --bg-color-dark: #181818;
  --text-color-light: #333333;
  --text-color-dark: #f4f4f4;
  --hover-color-light: #e0e0e0;
  --hover-color-dark: #333333;
  --border-color-light: #eaeaea;
  --border-color-dark: #2e2e2e;
}

.light {
  --bg-color: var(--bg-color-light);
  --text-color: var(--text-color-light);
  --hover-color: var(--hover-color-light);
  --border-color: var(--border-color-light);
}

.dark {
  --bg-color: var(--bg-color-dark);
  --text-color: var(--text-color-dark);
  --hover-color: var(--hover-color-dark);
  --border-color: var(--border-color-dark);
}

/* Logo da sidebar */
.logo {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--text-color);
  padding: 10px 0;
  border-bottom: 1px solid var(--border-color);
  cursor: pointer;
}

.logo span {
  transition: opacity 0.3s;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}

/* Menu */
.menu {
  list-style: none;
  padding: 0;
  margin: 20px 0;
  flex-grow: 1;
}

.menu li {
  margin: 15px 0;
}

.link {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1rem;
  color: var(--text-color);
  text-decoration: none;
  padding: 10px;
  border-radius: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.link:hover {
  background-color: var(--hover-color);
  color: var(--text-color);
}

/* Botões de alternância de tema e logout */
.themeButton,
.logoutButton {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 12px;
  border: none;
  border-radius: 8px;
  background-color: var(--hover-color);
  color: var(--text-color);
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  width: 100%;
  margin-top: 15px;
}

.themeButton:hover,
.logoutButton:hover {
  background-color: var(--text-color);
  color: var(--bg-color);
}

/* Responsividade: Colapsar a Sidebar em dispositivos móveis */
@media (max-width: 768px) {
  .sidebar {
    width: 80px;
    padding: 10px;
  }

  .menu li {
    justify-content: center;
  }

  .logo {
    justify-content: center;
  }

  .hidden {
    display: none;
  }

  .footer {
    display: flex;
    flex-direction: column;
  }
}
